
import { Estudiante } from './../../../@core/data/models/estudiante';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UbicacionesService } from '../../../@core/data/ubicaciones.service';
import { FORM_ESTUDIANTE } from './form-estudiante';
import { ToasterService, ToasterConfig, Toast, BodyOutputType } from 'angular2-toaster';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';
import Swal from 'sweetalert2';
import 'style-loader!angular2-toaster/toaster.css';

@Component({
  selector: 'ngx-crud-estudiante',
  templateUrl: './crud-estudiante.component.html',
  styleUrls: ['./crud-estudiante.component.scss'],
})
export class CrudEstudianteComponent implements OnInit {
  config: ToasterConfig;
  estudiante_id: number;

  @Input('estudiante_id')
  set name(estudiante_id: number) {
    this.estudiante_id = estudiante_id;
    this.loadEstudiante();
  }

  @Output() eventChange = new EventEmitter();

  info_estudiante: Estudiante;
  formEstudiante: any;
  regEstudiante: any;
  clean: boolean;

  constructor(private translate: TranslateService, private ubicacionesService: UbicacionesService, private toasterService: ToasterService) {
    this.formEstudiante = FORM_ESTUDIANTE;
    this.construirForm();
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      this.construirForm();
    });
   }

  construirForm() {
    this.formEstudiante.titulo = this.translate.instant('GLOBAL.estudiante');
    this.formEstudiante.btn = this.translate.instant('GLOBAL.guardar');
    for (let i = 0; i < this.formEstudiante.campos.length; i++) {
      this.formEstudiante.campos[i].label = this.translate.instant('GLOBAL.' + this.formEstudiante.campos[i].label_i18n);
      this.formEstudiante.campos[i].placeholder = this.translate.instant('GLOBAL.placeholder_' + this.formEstudiante.campos[i].label_i18n);
    }
  }

  useLanguage(language: string) {
    this.translate.use(language);
  }


  getIndexForm(nombre: String): number {
    for (let index = 0; index < this.formEstudiante.campos.length; index++) {
      const element = this.formEstudiante.campos[index];
      if (element.nombre === nombre) {
        return index
      }
    }
    return 0;
  }


  public loadEstudiante(): void {
    if (this.estudiante_id !== undefined && this.estudiante_id !== 0) {
      this.ubicacionesService.get('estudiante/?query=id:' + this.estudiante_id)
        .subscribe(res => {
          if (res !== null) {
            this.info_estudiante = <Estudiante>res[0];
          }
        });
    } else  {
      this.info_estudiante = undefined;
      this.clean = !this.clean;
    }
  }

  updateEstudiante(estudiante: any): void {

    const opt: any = {
      title: 'Update?',
      text: 'Update Estudiante!',
      icon: 'warning',
      buttons: true,
      dangerMode: true,
      showCancelButton: true,
    };
    Swal(opt)
    .then((willDelete) => {
      if (willDelete.value) {
        this.info_estudiante = <Estudiante>estudiante;
        this.ubicacionesService.put('estudiante', this.info_estudiante)
          .subscribe(res => {
            this.loadEstudiante();
            this.eventChange.emit(true);
            this.showToast('info', 'updated', 'Estudiante updated');
          });
      }
    });
  }

  createEstudiante(estudiante: any): void {
    const opt: any = {
      title: 'Create?',
      text: 'Create Estudiante!',
      icon: 'warning',
      buttons: true,
      dangerMode: true,
      showCancelButton: true,
    };
    Swal(opt)
    .then((willDelete) => {
      if (willDelete.value) {
        this.info_estudiante = <Estudiante>estudiante;
        this.ubicacionesService.post('estudiante', this.info_estudiante)
          .subscribe(res => {
            this.info_estudiante = <Estudiante>res;
            this.eventChange.emit(true);
            this.showToast('info', 'created', 'Estudiante created');
          });
      }
    });
  }

  ngOnInit() {
    this.loadEstudiante();
  }

  validarForm(event) {
    if (event.valid) {
      if (this.info_estudiante === undefined) {
        this.createEstudiante(event.data.Estudiante);
      } else {
        this.updateEstudiante(event.data.Estudiante);
      }
    }
  }

  private showToast(type: string, title: string, body: string) {
    this.config = new ToasterConfig({
      // 'toast-top-full-width', 'toast-bottom-full-width', 'toast-top-left', 'toast-top-center'
      positionClass: 'toast-top-center',
      timeout: 5000,  // ms
      newestOnTop: true,
      tapToDismiss: false, // hide on click
      preventDuplicates: true,
      animation: 'slideDown', // 'fade', 'flyLeft', 'flyRight', 'slideDown', 'slideUp'
      limit: 5,
    });
    const toast: Toast = {
      type: type, // 'default', 'info', 'success', 'warning', 'error'
      title: title,
      body: body,
      showCloseButton: true,
      bodyOutputType: BodyOutputType.TrustedHtml,
    };
    this.toasterService.popAsync(toast);
  }

}
